button {
    cursor: pointer;
}
.inputDiv{
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    box-shadow: 0px 8px 5px 0px #808080ba;
    padding: 20px;
    background: #e7e7e7d6;
    border-radius: 3px;
}
.inputDiv input {
    background-color: #dd9036fa;
    border-radius: 2px;
    cursor: pointer;
}
table{
    position: relative;
    top: 75px;
    border-collapse: collapse;
}
thead {
    background-color: #dd9036fa;
}
thead tr:hover {
    background-color: #cb7613fa;
}
tbody {
    background-color: #e7e7e7d6;
    color: #000;
}
tbody tr:hover {
    background-color: #c6c1c1;
}
th {
    padding: 5px 10px;
}
td {
    padding: 5px 10px;
}
tr,th,td {
    border: 1px solid black;
}
.jsonOutput {
    display: flex;
    position: absolute;
    top: 70px;
    left: 50%;
    width: 100%;
    transform: translate(-50%);
    background: #ffffffdb;
    flex-direction: column;
}
.headingDiv {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 75%;
    text-align: center;
    padding: 10px;
    box-shadow: 0px 20px 15px 0px #808080bf;
    background-color: #ebebeb;
    border-radius: 3px;
}
.headingDiv h1 {
    background-color: #de923a;
    padding: 30px 0px;
}
.jsonShowBtn {
    position: relative;
    top: 75px;
}
.closeJson {
    position: absolute;
    right: 100px;
    top: 70px;
}
.jsonOutput pre {
    text-align: left;
margin-left: 30px;
}
@media screen and (max-width: 600px) and (max-height: 900px) and (orientation: portrait) {
    .headingDiv {
        top: 50%;
        height: max-content;
        width: 100vw;
    }
    
    .headingDiv h1 {
        font-size: 14px;
    }
    .headingDiv p {
        font-size: 12px;
    }
  
}